.Features-content-top {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background: #fff;
    -webkit-box-shadow: 0px 12px 32px transparent;
    box-shadow: 0px 12px 32px transparent;
    border-radius: 5px;
    cursor: pointer;
    text-shadow: rgba(0,0,0,0.01) 0 0 1px;
    color: #707f8f;
    line-height: 27PX;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 17px;
    font-family: Poppins, sans-serif;
    font-weight: 600;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 16px;
    -webkit-transition: ease-in-out 200ms all;
    transition: ease-in-out 200ms all;
}

.Features-content-top.is-active {
    -webkit-box-shadow: 0px 12px 32px rgba(103,142,178,0.16);
    box-shadow: 0px 12px 32px rgba(103,142,178,0.16);
    margin-bottom: 32px;
    margin-top: 16px;
}

.Features-content-top:hover svg path, .Features-content-top.is-active svg path {
    stroke: #7b69c5;
}

.Feature-content-child {
    display: none;
}

.Features-content-top.is-active svg {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}
.Features-content-top svg {
    -webkit-transition: ease-in-out 200ms transform;
    transition: ease-in-out 200ms transform;
}

.Features-content-top:hover, .Features-content-top.is-active {
    color: #7b69c5;
}

.img-mobile-view{
    width: 100%;
}

@media (min-width: 899px){
    .Features-content section:not(:last-child) {
        border-bottom: 2px solid #EFF2F6;
    }
}

@media (max-width: 899px){
    .Features-content section {
        display: block !important;
        border-bottom: 1px solid #EFF2F6;
    }
}

@media (min-width: 899px){
    .Feature-content-child {
        display: none;
    }
}